.App {
  font-family: 'Libre Baskerville', serif;
  font-size: 1rem;
}

.Deck-wrapper {
  text-align: center;
}
.Deck-list {
  padding: 0.5em 0;
  list-style: none;
  text-align: center;
  margin-left: auto;
  margin-right: auto;
}

.Card {
  display: inline-block;
  width: 20em;
  padding: 1.5em;
  border-radius: 1em;
  -webkit-box-shadow: 0px 1px 3px rgba(255, 38, 38, 0.75), 0px 2px 6px rgba(255, 84, 84, 0.5);
          box-shadow: 0px 1px 3px rgba(255, 38, 38, 0.75), 0px 2px 6px rgba(255, 84, 84, 0.5);
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
  color: rgb(50, 50, 50);
  text-align: left;
  vertical-align: top;
  z-index: 1000;
  background-color: white;
}
.Card-fade-in {
  -webkit-animation-name: fade-in;
          animation-name: fade-in;
  -webkit-animation-duration: 0.2s;
          animation-duration: 0.2s;
  -webkit-animation-fill-mode: forwards;
          animation-fill-mode: forwards;
  -webkit-animation-iteration-count: 1;
          animation-iteration-count: 1;
}
.Card-transition {
  -webkit-transition: all ease-in-out 0.2s;
  -o-transition: all ease-in-out 0.2s;
  transition: all ease-in-out 0.2s;
}
.Card-prev {
  position: relative;
}
.Card-next {
  position: relative;
}
.Card-active {
  position: relative;
}
.Card-title {
  font-family: 'Inknut Antiqua', serif;
  font-size: 1.5em;
  margin: 0;
  height: 1.5em;
  line-height: 1.5em;
  overflow-x: hidden;
  overflow-y: hidden;
  white-space: nowrap;
}

.Card-subtitle {
  margin: 1em 0 0.5em 0;
  font-size: 1em;
  font-style: italic;
}

.Card-details {
  margin: 0.5em 0;
}

.Card-detail {
  color: rgb(70, 70, 70);
  font-size: 0.8em;
  border-bottom: 1px solid rgb(70, 70, 70);
  padding: 0.2em 0;
}
.Card-detail {
  font-weight: bold;
}
.Card-detail > span {
  float: right;
  font-weight: normal;
}

.Card-description {
  height: 15em;
  overflow-y: auto;
  -webkit-overflow-scrolling: touch;
}

.Card-text {
  font-size: 0.8em;
  margin: 0.5em 0;
}

.Card-text:last-child {
  text-align: right;
}

/* Navbar and buttons */
.Navbar {
  width: 100%;
  height: 3em;
  display: -ms-flexbox;
  display: flex;
  font-size: 0.9em;
  padding: 0.25em;
  -ms-flex-pack: center;
      justify-content: center;
}
.Btn {
  font-size: 1em;
  margin: 0 0.25em;
  background-color: red;
  color: white;
  display: inline-block;
  padding: 0.7em 1em;
  border: none;
  border-radius: 100em;
  -ms-flex-item-align: center;
      align-self: center;
  outline: none;
  -webkit-transition: all ease-in-out 0.2s;
  -o-transition: all ease-in-out 0.2s;
  transition: all ease-in-out 0.2s;
}
.Btn:focus, .Btn:hover {
  background-color: rgb(199, 0, 0);
}
.Btn:disabled {
  background-color: rgb(185, 185, 185);
}

@-webkit-keyframes fade-in {
  from {
    opacity: 0;
    -webkit-transform: scale(0.6);
            transform: scale(0.6)
  }
  to {
    opacity: 1;
    -webkit-transform: scale(1);
            transform: scale(1)
  }
}

@keyframes fade-in {
  from {
    opacity: 0;
    -webkit-transform: scale(0.6);
            transform: scale(0.6)
  }
  to {
    opacity: 1;
    -webkit-transform: scale(1);
            transform: scale(1)
  }
}


